body {
  background-color: #77b892;
}

.app {
  width: 100%;
  height: 100%;
}

.app-container {
  z-index: 1;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.app-wrapper {
  height: 100%;
  padding-top: 150px;
  width: 750px;
}

.search_container {
  width: 100%;
}

.app-container ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: left;
}

.container {
  background-color: white;
  border-radius: 4px;
  padding: 5px;
  margin-top: 2px;
}

.container li {
  font-size: 20px;
}

.container li:hover {
  background: black;
  color: white;
}

.search {
  width: 100%;
  border: none;
  padding: 5px;
  border-radius: 4px;
  font-size: 20px;
  outline: none;
}

.hide-button {
  display: none;
}

.select-css {
  float: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  padding: 0.6em 1.4em 0.5em 0.8em;
  max-width: 100%;
  box-sizing: border-box;
  margin: 0;
  border: 1px solid #aaa;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  border-radius: 5px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: #fff;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E"),
    linear-gradient(to bottom, #ffffff 0%, #ffffff 100%);
  background-repeat: no-repeat, repeat;
  background-position: right 0.5em top 50%, 0 0;
  background-size: 0.65em auto, 100%;
}
.select-css::-ms-expand {
  display: none;
}
.select-css:hover {
  border-color: #888;
}
.select-css:focus {
  border-color: #aaa;
  box-shadow: 0 0 1px 3px rgba(59, 153, 252, 0.7);
  box-shadow: 0 0 0 3px -moz-mac-focusring;
  color: #222;
  outline: none;
}
.select-css option {
  font-weight: normal;
}

.convert {
  background-color: #fff;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 30px;
}

.convert-container label {
  display: flex;
  flex-direction: row;
  margin-left: 10px;
}

.labelClass {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.labelClass div {
  display: flex;
  flex-direction: row;
}

.convert-container {
  display: flex;
  flex-direction: column;
}

.button-css {
  float: right;
  width: 200px;
  margin-top: 10px;
  padding: 10px;
  cursor: pointer;
  border: none;
  background-color: #fff;
  border-radius: 4px;
}

input[type="radio"] {
  margin-right: 5px;
}

.button-wrapper {
  width: 100%;
}

.output-container {
  margin-top: 250px;
  display: flex;
  justify-content: center;
}

.output {
  font-size: 4rem;
  font-weight: 500;
}

@media screen and (max-width: 800px) {
  .app-wrapper {
    height: 100%;
    padding: 150px 20px 0 20px;
    width: 100%;
  }
  .container {
    padding: 5px;
  }
  .convert {
    padding: 10px;
  }
  p {
    font-size: 12px;
  }
  .convert input[type="text"] {
    width: 120px;
  }
  .output-container {
    margin-top: 150px;
  }
  .output {
    font-size: 1.5rem;
    font-weight: 500;
  }
}
